<template>
	<div class="about">
		<h1>{{title}}</h1>
		<h2>{{subTitle}}</h2>
		<h3>{{subTitle2}}</h3>
		<Input type="text" v-model="input" placeholder="Username">
			<Icon type="ios-person-outline" slot="prepend"></Icon>
		</Input>
		<p>{{input}}</p>
	
	</div>
</template>


<script>
	import {
		computed,
		ref,
		reactive,
		toRefs,
		watch,
		onMounted,
		onUpdated,
		onUnmounted
	} from "@vue/composition-api";
	import mainNavBar from '../components/mainNavBar';
	export default {
		name: "about",
		components:{
			mainNavBar
		},
		setup() {
			onMounted(() => {
				console.log("mounted!");
			});
			onUpdated(() => {
				console.log("updated!");
			});
			onUnmounted(() => {
				console.log("unmounted!");
			});
			const subTitle = ref("this is a subTitle");
			const subTitle2 = computed(() => subTitle);
			const data = reactive({
				title: "this is a title",
				subTitle,
				subTitle2,
				input: ""
			});
			watch(() => data.input, (newVal, oldVal) => console.log(newVal));

			return {
				...toRefs(data)
			};
		}
	};
</script>
